import React, {Component} from 'react';
import Modal from 'react-modal';
import {observer} from 'mobx-react';
import '../styles/form.css';

export default observer(class NewRecipeForm extends Component {
  
    constructor() {
      super();
    }
  
    closeModal() {
      //this.props.store.modalIsOpen = observable(false);
    }
    
    componentWillReact() {
      console.log("changing")
    }
    
    render(){
      return  (
          <div className="modal-container">
            <Modal isOpen={this.props.store.modalIsOpen.value} className="modal">
              <div className="modal-header">
              <h3>Add a recipe</h3>
              <i className="fa fa-times fa-2x" onClick={this.closeModal.bind(this)}></i>
              </div>
              <form className="recipe-form">
                <div className="form-group">
                  <h3>Name</h3>
                  <input id="name" placeholder="Enter recipe name" />
                </div>
                <div className="form-group">
                  <h3>Ingredients (separate with commas)</h3>
                  <input id="ingredients" placeholder="Enter ingredients" />
                </div>
                <div className="form-group">
                  <h3>Instructions</h3>
                  <textarea id="instructions" rows="4" placeholder="Recipe instructions"></textarea>
                </div>
                <div className="form-group">
                  <h3>Time</h3>
                  <input id="time" placeholder="Estimated time"/>
                </div>
                <div className="form-group">
                  <h3>Image Link</h3>
                  <input id="url" placeholder="Image URL"/>
                </div>
                <div id="buttons" className="form-group">
                
                  <a href="#" id="submit">Submit</a>
                  <a href="#" id="cancel">Cancel</a>
                </div>
              </form>
            </Modal>
          </div>
        );
      }
});


