import React, {Component} from 'react';
import {observer} from 'mobx-react';
import '../styles/navbar.css';

export default observer(class Navbar extends Component {
    constructor(props) {
        super(props);
        setInterval(() => {
            this.props.store.incrementCounter()
        },1000);
        this.state = {
            show: {
                
            },
            hide: {
                maxHeight: 0,
                
            },
            showing: false
        }
    }
    
    openModal() {
        // this.props.store.modalIsOpen = ;
        this.props.store.openModal();
    }
    
    toggleMenu() {
        this.setState({showing: !this.state.showing});
    }
    
    
     render(){
      return  (
      <div>
        <div className="nav-container">
          <h3>Recipe App</h3>
          <ul>
            <li onClick={this.openModal.bind(this)}><a>Add Recipe</a></li>
            <li><a>Home</a></li>
            <li><a>{this.props.store.counter.value}</a></li>
            <li><a>Contact Us</a></li>
            <li id="hamburger" onClick={this.toggleMenu.bind(this)}>
              
                <div className="bar-container">
                    <div className="bar"></div>
                    <div className="bar"></div>
                    <div className="bar"></div>
                </div>
              
            </li>
    
          </ul>
                
          </div>
          <ul id="mobile-menu" style={this.state.showing ? this.state.show : this.state.hide}>
                    <li><a href="#">New Recipe</a></li>
                    <li><a href="#">Home</a></li>
                    <li><a href="#">About</a></li>
                    <li><a href="#">Contact Us</a></li>
         </ul>
        </div>);
      }
});


