import {observable, action} from 'mobx';

class ModalStore {
    constructor() {
        this.modalIsOpen = observable(false);
        this.counter = observable(0);
    }
    
    incrementCounter() {
        this.counter.value++;
    }
    
    closeModal() {
        this.modalIsOpen.value = false;
    }
    
    openModal() {
        console.log('open')
        this.modalIsOpen.value = true;
    }
}

export default new ModalStore();


